@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Montserrat:wght@500;600&family=Roboto:wght@700&display=swap');
.quote {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px;
  padding-top: 49px;
}
.top {
  position: absolute;
  top: 0;
  color: #ff6b08;
  text-align: center;
  font-family: Abril Fatface;
  font-size: 59px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.text {
  display: block;
  width: 229px;
  height: 48px;
  color: #242a37;
  text-align: center;
  font-family: Montserrat;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.underline {
  display: block;
  width: 100px;
  height: 1px;
  background: rgba(36, 42, 55, 0.5);
}
.underText {
  color: #898f9f;
  text-align: center;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
@media screen and (min-width: 768px) {
  .text {
    padding-top: 126px;
  }
  .top {
    top: 106px;
  }
}
@media screen and (min-width: 1280px) {
  .quote {
    padding-left: 167px;
  }
  .text {
    padding-top: 0;
  }
  .top {
    top: 0;
  }
}
