@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Montserrat:wght@500&display=swap');
.header {
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  background: #fff;
  box-shadow: 0px 2px 3px 0px rgba(9, 30, 63, 0.1);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 58px;
}
.link {
  color: #242a37;
  font-family: Abril Fatface;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
}
.userBox {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 14px;
}
.item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  text-align: center;
  background-color: #f5f7fa;
  color: #242a37;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.breake {
  display: block;
  width: 1px;
  height: 33px;
  background: #e0e5eb;
}
.logout {
  border: none;
  background-color: transparent;
  color: #242a37;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  text-decoration-line: underline;
}
@media screen and (min-width: 768px) and (max-width: 1279px) {
  /* Your CSS styles for screen widths between 321px and 767px go here */
  .header {
    padding-left: 32px;
    justify-content: flex-start;
  }
  /* Add more styles for other elements as needed */
}
@media screen and (min-width: 1280px) {
  /* Your CSS styles for screen widths between 321px and 767px go here */
  .header {
    padding-left: 32px;
    justify-content: flex-start;
  }
  /* Add more styles for other elements as needed */
}
